export function getEnvironment(): IEnvironment {
    const environment = {
        ...defaultEnvironment,
        STORAGE_NAME: (getProject().projectId === '')
            ? 'TVM-STATE'
            : `${getProject().projectId.toUpperCase()}-TVM-STATE`,
        ...(<any>window).environment,
        production: (document.querySelector('body.production') !== null)
    };
    return environment;
}
